/* ===============
	VARIABLES
	=============== */
/* ===============
	MAIN-PAGE
	=============== */
body {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  background: #eeeeee; }

.app {
  height: 100vh;
  display: block; }

/* ===============
	SIDE-BAR
	=============== */
.sidebar {
  background: #444444;
  width: 300px;
  margin-top: 0;
  height: 100%;
  position: relative; }

.show input {
  position: absolute;
  margin-top: 16px; }

.hide input {
  position: absolute;
  margin-top: 16px;
  display: none; }

/* ===============
	MENU
	=============== */
.menu-item {
  padding: 15px 25px;
  cursor: pointer;
  color: #ffffff;
  font-size: 13px;
  text-shadow: 0 0 6px rgba(255, 255, 255, 0.2);
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  border-top: 1px solid #555555;
  border-right: 1px solid #555555;
  border-bottom: 1px solid #222222;
  border-left: 1px solid #222222;
  z-index: 2;
  -webkit-transition: 1s ease;
  -o-transition: 1s ease;
  transition: 1s ease; }
  .menu-item:hover {
    background: #222222; }
    .menu-item:hover:after {
      content: '';
      background: #999999;
      position: absolute;
      left: 0;
      margin-top: -15px;
      width: 300px;
      height: 45px;
      -webkit-animation: efect .8s ease;
              animation: efect .8s ease;
      opacity: 0; }

/* ===============
		DROPDOWN-MENU
		=============== */
.sub-menu {
  display: none;
  -webkit-animation: slideIn .4s ease;
          animation: slideIn .4s ease;
  z-index: 1; }
  .sub-menu .sub-menu-item {
    padding: 15px 50px;
    cursor: pointer;
    color: #ffffff;
    font-size: 13px;
    text-shadow: 0 0 6px rgba(255, 255, 255, 0.2);
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    border-top: 1px solid #555555;
    border-right: 1px solid #555555;
    border-bottom: 1px solid #222222;
    border-left: 1px solid #222222;
    z-index: 2;
    -webkit-transition: 1s ease;
    -o-transition: 1s ease;
    transition: 1s ease;
    background: #333333; }
    .sub-menu .sub-menu-item:hover {
      background: #222222; }
      .sub-menu .sub-menu-item:hover:after {
        background: #999999;
        content: '';
        position: absolute;
        left: 0;
        margin-top: -15px;
        width: 300px;
        height: 45px;
        -webkit-animation: efect .8s ease;
                animation: efect .8s ease;
        opacity: 0; }

/* ===============
	HOVER EFECT ANIMATION
	=============== */
@-webkit-keyframes efect {
  from {
    width: 0;
    opacity: .1; }
  to {
    width: 300px;
    opacity: 0; } }
@keyframes efect {
  from {
    width: 0;
    opacity: .1; }
  to {
    width: 300px;
    opacity: 0; } }

/* ===============
	SUB-MENU ANIMATION 
	=============== */
@-webkit-keyframes slideIn {
  from {
    margin-top: -20px; }
  to {
    margin-top: 0; } }
@keyframes slideIn {
  from {
    margin-top: -20px; }
  to {
    margin-top: 0; } }

/* ===============
	CHEVRON ARROW ICON
	=============== */
svg {
  float: right;
  -webkit-transition: .2s ease;
  -o-transition: .2s ease;
  transition: .2s ease; }

/* ===============
	TOGGLE DROPDOWN AND ROTATE ARRROW
	=============== */
input[type=checkbox]:checked + .menu .sub-menu {
  display: block; }

input[type=checkbox]:checked + .menu .menu-item svg {
  -webkit-transform: rotate(90deg);
      -ms-transform: rotate(90deg);
          transform: rotate(90deg); }

/* ===============
	CHANGE BACKGROUND WHEN ITEM IS SELECTED
	=============== */
input[type=radio]:checked + .menu-item {
  background: #0f90bf;
  border-top: 1px solid #22b1e5;
  border-right: 1px solid #22b1e5;
  border-bottom: 1px solid #095875;
  border-left: 1px solid #095875; }

input[type=radio]:checked + .sub-menu-item {
  background: #0f90bf;
  border-top: 1px solid #22b1e5;
  border-right: 1px solid #22b1e5;
  border-bottom: 1px solid #095875;
  border-left: 1px solid #095875; }

.btn-toggle-input {
  cursor: pointer;
  color: #bbbbbb;
  padding: 15px 25px;
  -webkit-transition: .8s ease;
  -o-transition: .8s ease;
  transition: .8s ease; }
  .btn-toggle-input:hover {
    color: white; }



